import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    lexicon: {
        nav: {
            home: '',
            tracklist: '',
            settings: ''
        },
        home: {
            newproducts: '',
            bestsellers: '',
            direct: '',
            track: '',
            tracked: '',
            search: '',
            searchAlert: '',
            notFound: ''
        },
        track: {
            title: '',
            cover: '',
            name: '',
            price: '',
            action: '',
            twd: '',
            direct: '',
            delete: '',
            deleteAlert: '?',
            notTrack: '',
            totalNumber: '',
            totalAmount: '',
            clearTracked: '',
            clearConfirm: '?',
            checkAlert: ''
        },
        settings: {
            title: '',
            language: '',            
            languageFamily: 'zh',
        }
    },
}

// settingsSlice
export const settingsSlice = createSlice({
    name: 'settings',
    initialState,
    reducers: {
        setLanguage: (state, action) => ({...state, lexicon: {...action.payload}})
    }
});

// action creators
export const { setLanguage } = settingsSlice.actions;

// reducer
export default settingsSlice.reducer;